<template>
    <d3box class="FORM-VIEW">
            <div id="nameBox">
                <label >
                    Your name please:
                    <input  @keydown.enter="submit('name')"  type="text" name="name" placeholder="name" id="name"  spellcheck="false">
                </label>
                <button CLASS='NEXT-BUTTON' @click="()=>submit('name')">NEXT</button>
            </div>
            <div id="rollNumberBox">
                <label >
                    whats what Number:
                    <input  @keydown.enter="submit('rollNumber')" type="text" name="rollNumber"  placeholder="rollnumber" id="rollNumber" spellcheck="false">
                </label>
                <button CLASS='NEXT-BUTTON'  @click="submit('rollNumber')">NEXT</button>
            </div>
            <div id="fathersNamebox">
                <label >
                    who is your father?:
                    <input  @keydown.enter="submit('fathersName')" type="text" name="fathersName"  placeholder="🙏" id="fathersName" spellcheck="false">
                </label>
                <button CLASS='NEXT-BUTTON'  @click="()=>submit('fathersName')">NEXT</button>
            </div>
            <div id="phoneNumberbox">
                <label >
                    when can we call?:
                    <input  @keydown.enter="submit('phoneNumber')" type="text" name="phoneNumber" placeholder="phone number" id="phoneNumber" spellcheck="false">
                </label>
                <button CLASS='NEXT-BUTTON'  @click="()=>submit('phoneNumber')">NEXT</button>
            </div>
            <div id="emailbox">
                <label >
                    at last:
                    <input  @keydown.enter="submit('email')" type="email" name="email" placeholder="your email?" id="email" spellcheck="false">
                </label>
                <button CLASS='NEXT-BUTTON'  @click="()=>submit('email')">NEXT</button>
            </div>
            <div id="verifybox">
                <button id="verify">VERIFY VIA GOOGLE</button>
                <button CLASS='NEXT-BUTTON'>NEXT</button>
            </div>
    </d3box>
</template>
<style lang="less">
.FORM-VIEW 
{
    text-align: center;
    & > .D3-CUBE-ROOT >     div
    {
            display: grid;
            border-radius: 34px;
            box-shadow: 0px 0px 5px 3px white;
            padding: 14vmin;
            justify-content: center;
            align-content: space-around;
            align-items: center;
            justify-items: center;
    }
    input
    {
        BACKGROUND: radial-gradient(rgba(209, 191, 191, 0.19), rgba(97, 88, 88, 0.5));
        border: solid 1px black;
        box-shadow: 0px 0px 5px rgba(0,0,0,1) inset;
        height: 4em;
        color: white;
        width:calc(var(--cube-face-length) * 0.80);
        padding-left: 1em;
        
        &:focus
        {
            background:black
        }
    }    
    button
    {
            height:calc(var(--cube-face-length) / 5);
            width: calc(var(--cube-face-length) / 3);
            background: black;
            border: none;
            color: white;
            box-shadow: 0px 0px 1px white;
            &:active
            {
                background:wheat
            }
            
    border-radius: 5px;
    }  
}
</style>
<script>

//fixme: this module needs to be seriusly worked upon
import d3box from './i.v.s.box.3d.cube.form'
export default {
    name:'FORM-VIEW',
    components:{
        d3box
    },
    data()
    {
        return {
            data:{name:null,
                fathersName:null,
                phoneNumber:null,
                rollNumber:null,
                email:null,
                verify:null
                },
            faces:['name','rollNumber','fathersName','phoneNumber','email','verify']
        }
    },
    methods:{
        error(where)
        {
            console.error(where)
            this.$el.querySelector('#'+where).style.border='solid red';
            this.$el.querySelector('#'+where).focus();
        },
        focus(name)
        {
            this.$el.querySelector('#'+name).focus()
            return this.$children[0].showFace(this.faces.indexOf(name));
        },
        verify(what,data)
        {
            var data=data||'';
            data=data.toLowerCase()
            switch(what)
            {
                case 'name':return /[a-zA-Z/s].+/.test(data);break;
                case 'fathersName': return  /[a-zA-Z/s].+/.test(data);break;
                case 'phoneNumber':return /[\+\d].{10,13}/.test(data);break;
                case 'email':return /[a-zA-Z0-9\.]{4,20}@(gmail)|(outlook)\.com/.test(data);break;
                case 'rollNumber':return true;break;
                default :return false;
            }
        },
        showNext()
        {
            for(let each in this.data)
            {
                if(!this.data[each])
                {
                    return  this.focus(each)
                }
            }
            this.$say('formdone',this.data);
        },
        submit(what)
        {
            var value=this.$el.querySelector('#'+what).value;
            if(!this.verify(what,value))return this.error(what);
                this.data[what]=value;
            this.showNext();
        }
    },
    mounted()
    {
        this.showNext();
    }

}
</script>


