<template>
    <div class="D3-CUBE-CAMERA ROOT" >
        <DIV class="D3-CUBE-ROOT" :style="{
            transform:currentFace
        }">
            <slot></slot>
                         
        </DIV>    
    </div>
</template>
<script>
export default {
    name:"D3-CUBE-VIEW",
    data()
    {
        return  {
            currentFace:'rotateX(0deg)  rotateY(0deg)'
        }
    },
    methods:{
        showFace(faceNumber)
        {
            if(faceNumber<0||faceNumber>6)throw "error";
                this.currentFace=this.faceStore['face'+faceNumber];

        },
        rotate()
        {
            var n=0;
            setInterval(()=>this.showFace(n++%6),1000);
        }
    },
    computed:{
        faceStore()
        {
        return {
                face0:'rotateX(0deg)  rotateY(0deg)',
                face1:'rotateX(0deg) rotateY(-90deg)',
                face2:'rotateX(0deg) rotateY(180deg)',
                face3:'rotateX(0deg) rotateY(90deg)',
                face4:'rotateX(-90deg) rotateY(0deg)',
                face5:'rotateX(90deg) rotateY(0deg)',
            }
        }
    },
    mounted()
    {
        let cube=this.$el.querySelector(".D3-CUBE-ROOT")
        this.$el.onpointerdown=function()
        {
            this.onpointerup=function()
            {
                this.onpointerup=this.onpointermove=null
            }
            this.onpointermove=function(mouse){
                let dy=mouse.movementX;
                let dx=mouse.movementY;
                
                let ry=dy+this.y||0;
                let rx=dx+this.x||0;
                this.x=rx%360;
                this.y=ry%360;
                // console.log(`rotateX(${rx}deg) this.rotateY(${ry}deg)`)
                cube.style.transform=`rotateX(${-rx}deg) rotateY(${ry}deg)`
            }
        }
        // window.ondeviceorientation=function(e)
        // {   
        //      cube.style.transform=`rotateX(${-rx}deg) rotateY(${90}deg)`
        // }
    }
}
</script>
<style lang="less">
body
{
    background-image: url('../assets/wall/k2.jpg');
    background-size: cover;
    background-position: center
}
.D3-CUBE-CAMERA
{
    // transition: all 3s ease;
    user-select: none;
    --cube-face-length:35vmin;
    height:100vh;
    width:100vw;
    perspective: var(--cube-perspective,100em);
    display: grid;
    justify-content: center;
    align-content: center;
            color:white;
    & > .D3-CUBE-ROOT
    {

        position: relative;
        transform-style: preserve-3d;
        transform-origin: center;
         & > *
        {
                border: dotted 1px #151127;
            background-image: radial-gradient(rgba(0,0,0, 0.51), rgba(0,0,0,1));
            width: var( --cube-face-length);
            height: var( --cube-face-length);
            position: absolute;
            transform-origin: 50% 50% calc(var(--cube-face-length)*-0.5);
            display: grid;
            justify-content: center;
            align-content: center;
            &:nth-child(1)
            {
                transform:translate3d(-50%,-50%,calc(var(--cube-face-length) * 0.5))
                    rotate3d(0,1,0,0deg);
            }
            &:nth-child(2)
            {
                transform:translate3d(-50%,-50%,calc(var(--cube-face-length) * 0.5))
                    rotate3d(0,1,0,90deg);
            }
            &:nth-child(3)
            {
                transform:translate3d(-50%,-50%,calc(var(--cube-face-length) * 0.5))
                    rotate3d(0,1,0,180deg);
            }
            &:nth-child(4)
            {
                transform:translate3d(-50%,-50%,calc(var(--cube-face-length) * 0.5))
                    rotate3d(0,1,0,270deg);
            }
            &:nth-child(5)
            {
                transform:translate3d(-50%,-50%,calc(var(--cube-face-length) * 0.5))
                    rotate3d(1,0,0,90deg);
            }
            &:nth-child(6)
            {
                transform:translate3d(-50%,-50%,calc(var(--cube-face-length) * 0.5))
                    rotate3d(1,0,0,270deg);
            }
        }

    }
}
</style>



